import { WorkingHour, ResponseDetail, Restaurant } from '@models';
import { RestaurantWorkingHoursPayload } from '@payloads';
import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  FormBuilder,
  FormControl,
  FormArray,
  FormGroup,
  AbstractControl,
  Validators
} from '@angular/forms';
import { Regex24Hours } from '@constants';
import { RestaurantService } from '@services';
import { MatRadioChange } from '@angular/material/radio';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { MatSnackBar } from '@angular/material/snack-bar';
import { map } from 'rxjs/operators';
import { Subscription } from 'rxjs';
import { ScheduleTypeEnum } from './enums/schedule-type.enum';

@Component({
  selector: 'app-schedules',
  templateUrl: './schedules.component.html',
  styleUrls: ['./schedules.component.scss']
})
export class SchedulesComponent implements OnInit, OnDestroy {
  public weekdays = [
    {
      name: 'Lunes',
      id: 1
    },
    {
      name: 'Martes',
      id: 2
    },
    {
      name: 'Miércoles',
      id: 3
    },
    {
      name: 'Jueves',
      id: 4
    },
    {
      name: 'Viernes',
      id: 5
    },
    {
      name: 'Sábado',
      id: 6
    },
    {
      name: 'Domingo',
      id: 7
    }
  ];
  public isLoading: boolean;
  public workingHoursForm = this.fb.group({
    scheduleType: new FormControl(0),
    mixedHours: new FormArray([]),
    fixedHours: new FormGroup({
      weekdays: this.fixedHoursWeekdays(),
      opening: new FormControl('', [
        Validators.required,
        Validators.pattern(Regex24Hours)
      ]),
      closing: new FormControl('', [
        Validators.required,
        Validators.pattern(Regex24Hours)
      ])
    })
  });
  private restaurantId: number = parseInt(
    localStorage.getItem('restaurantId'),
    10
  );
  public workingHours: Array<WorkingHour> = [];
  private restaurantDetailSubscription = Subscription.EMPTY;
  private setRestaurantSchedulesSubscription = Subscription.EMPTY;
  public scheduleTypeEnum = ScheduleTypeEnum;

  constructor(
    private fb: FormBuilder,
    private restaurantService: RestaurantService,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    this.isLoading = true;
    this.setUpMixedHoursWeekdays();

    this.restaurantDetailSubscription = this.restaurantService
      .getRestaurantDetail(this.restaurantId)
      .pipe(
        map((response: ResponseDetail) => {
          return new Restaurant(response.restaurant);
        })
      )
      .subscribe((restaurant: Restaurant) => {
        this.workingHours = restaurant.working_hours;
        this.isLoading = false;
        if (this.workingHours) {
          this.checkScheduleType();
        }
      });
  }

  ngOnDestroy() {
    this.restaurantDetailSubscription.unsubscribe();
    this.setRestaurantSchedulesSubscription.unsubscribe();
  }

  /**
   *  Returns FormGroup for Fixed Hours
   *
   * @returns AbstractControl Object
   */
  get fixedHoursWeekdaysFormGroup(): AbstractControl {
    return this.workingHoursForm.get('fixedHours').get('weekdays') as FormGroup;
  }

  /**
   *  Returns Mixed Hours Form Array
   * @returns Form Array
   */
  get mixedHoursWeekdaysFormArray(): FormArray {
    return this.workingHoursForm.get('mixedHours') as FormArray;
  }

  /**
   *  Returns Form Control for schedule type (fixed or mixed hours)
   * @returns Form Control
   */
  get scheduleTypeFormControl(): FormControl {
    return this.workingHoursForm.get('scheduleType') as FormControl;
  }

  /**
   * For mixed hours schedule type, when a day is selected or deselected,
   * updates closing and opening hours input validators for that day.
   */
  public onMixedHoursDaySelect(event: MatCheckboxChange, index: number): void {
    if (event.checked) {
      this.mixedHoursWeekdaysFormArray.controls[index]
        .get('opening')
        .setValidators([Validators.required, Validators.pattern(Regex24Hours)]);
      this.mixedHoursWeekdaysFormArray.controls[index]
        .get('closing')
        .setValidators([Validators.required, Validators.pattern(Regex24Hours)]);
    } else {
      this.mixedHoursWeekdaysFormArray.controls[index].get('opening').reset();
      this.mixedHoursWeekdaysFormArray.controls[index].get('closing').reset();
      this.mixedHoursWeekdaysFormArray.controls[index]
        .get('opening')
        .clearValidators();
      this.mixedHoursWeekdaysFormArray.controls[index]
        .get('closing')
        .clearValidators();
    }

    this.mixedHoursWeekdaysFormArray.controls[index]
      .get('opening')
      .updateValueAndValidity();
    this.mixedHoursWeekdaysFormArray.controls[index]
      .get('closing')
      .updateValueAndValidity();
  }

  /**
   * When a schedule type is selected, updates the the validators for the form
   */
  public onScheduleTypeChange(event: MatRadioChange): void {
    const value = event.value;
    const validators = [Validators.required, Validators.pattern(Regex24Hours)];
    const fixedHours = this.workingHoursForm.get('fixedHours');

    if (value === this.scheduleTypeEnum.FixedHours) {
      fixedHours.get('opening').setValidators(validators);
      fixedHours.get('closing').setValidators(validators);
    } else {
      fixedHours.get('opening').clearValidators();
      fixedHours.get('closing').clearValidators();
    }
    fixedHours.get('opening').updateValueAndValidity();
    fixedHours.get('closing').updateValueAndValidity();
  }

  /**
   *  Saves changes. Saves new working hours
   */
  public onSubmit(): void {
    if (this.workingHoursForm.invalid) {
      return this.openSnackBar('Revisa los datos', 'Entendido');
    }
    this.isLoading = true;
    const payload = new RestaurantWorkingHoursPayload(this.workingHoursForm);

    if (
      this.workingHoursForm.get('scheduleType').value ===
      this.scheduleTypeEnum.FixedHours
    ) {
      if (this.checkSelectedFixedDays().length === 0) {
        this.isLoading = false;
        return this.openSnackBar(
          'Debes seleccionar por lo menos un día de la semana',
          'Entendido'
        );
      }
      this.setRestaurantSchedulesSubscription = this.restaurantService
        .setRestaurantSchedules(payload.fixedHoursJson(), this.restaurantId)
        .subscribe(() => {
          this.openSnackBar(
            'La información se actualizó con éxito',
            'Entendido'
          );
          this.isLoading = false;
        });
    } else {
      if (this.checkSelectedMixedDays().length === 0) {
        this.isLoading = false;
        return this.openSnackBar(
          'Debes seleccionar por lo menos un día de la semana',
          'Entendido'
        );
      }

      this.setRestaurantSchedulesSubscription = this.restaurantService
        .setRestaurantSchedules(payload.mixedHoursJson(), this.restaurantId)
        .subscribe(() => {
          this.openSnackBar(
            'La información se actualizó con éxito',
            'Entendido'
          );
          this.isLoading = false;
        });
    }
  }

  /**
   * Groups working hours from server response by closing and opening hour
   * to determine which FormGroup to patch (fixed hours or mixed hours)
   */
  private checkScheduleType(): void {
    const workingHoursArrays = this.groupBy(
      this.workingHours,
      (hour: WorkingHour) => {
        return [hour.opensAt, hour.closesAt];
      }
    );
    if (workingHoursArrays.length > 1) {
      this.patchFormWithMixedHours(workingHoursArrays);
    } else {
      this.patchFormWithFixedHours(workingHoursArrays);
    }
  }

  /**
   *  Creates an array of string with the weekdays id's which then is used to validate
   * the user has selected at least one day in the fixed hours schedule type
   *
   *  @returns Array of strings with selected days
   */
  private checkSelectedFixedDays(): Array<string> {
    const objects = Object.keys(
      this.workingHoursForm.value.fixedHours.weekdays
    ).filter((day) => {
      const fixedHours = this.workingHoursForm.get('fixedHours');
      return fixedHours.get('weekdays').get(day).value === true;
    });

    return objects;
  }

  /**
   *  Creates an array of Abstract Controls which then is used to validate
   * the user has selected at least one day in the mixed hours schedule type
   *
   *  @returns Array of AbstractControls with active days
   */
  private checkSelectedMixedDays(): Array<AbstractControl> {
    const currentDays = this.workingHoursForm.get('mixedHours') as FormArray;

    const objects = currentDays.controls.filter((day) => {
      return day.get('isSelected').value === true;
    });

    return objects;
  }

  /**
   * Creates the fixed hours Form Group for the working hours form
   *
   *  @returns A Form Group with a Form Control for each day of the week
   */
  private fixedHoursWeekdays(): FormGroup {
    const formGroupObject: { [key: string]: FormControl } = {};

    for (const weekday of this.weekdays) {
      formGroupObject[weekday.id] = new FormControl(false);
    }

    return new FormGroup(formGroupObject);
  }

  /**
   * Groups the existing working hours by opening and closing time.
   * This will be used to determine weather the schedule type is fixed or mixed.
   *
   *  @param array An array of Working Hours
   *  @param f A function to create the array names
   *  @returns fAn array containing an array for each group of days that opens
   *  and closes at the same time
   */
  private groupBy(
    array: Array<WorkingHour>,
    f: any
  ): Array<Array<WorkingHour>> {
    const groups: {
      [key: string]: Array<WorkingHour>;
    } = {};

    array.forEach((hour: WorkingHour) => {
      const group: string = JSON.stringify(f(hour));

      groups[group] = groups[group] || [];
      groups[group].push(hour);
    });

    return Object.keys(groups).map((group) => {
      return groups[group];
    });
  }

  /**
   * Created a snackbar event to display a message for the user
   *
   *  @param message Text that will be displayed as the main message in the snackbar
   *  @param action Text for the dismiss button on the snack bar
   */
  private openSnackBar(message: string, action: string): void {
    this.snackBar.open(message, action, {
      duration: 2000
    });
  }

  /**
   * Patches the form with existing fixed hours schedule.
   *
   *  @param workingHoursArrays An array of arrays containing the current working hours
   */
  private patchFormWithFixedHours(
    workingHoursArrays: Array<Array<WorkingHour>>
  ): void {
    this.workingHoursForm.patchValue({
      scheduleType: this.scheduleTypeEnum.FixedHours
    });

    const day = workingHoursArrays[0];

    workingHoursArrays.forEach((hourGroup: Array<WorkingHour>) => {
      hourGroup.forEach((hour: WorkingHour) => {
        const weekdayId = hour.weekDay.toString();
        const object: { [key: string]: boolean } = {};
        object[weekdayId] = true;

        this.workingHoursForm
          .get('fixedHours')
          .get('weekdays')
          .patchValue(object);
      });
    });

    this.workingHoursForm.get('fixedHours').patchValue({
      opening: day[0].opensAt,
      closing: day[0].closesAt
    });
  }

  /**
   * Patches the form with existing mixed hours schedule.
   *
   *  @param workingHoursArrays An array of arrays containing the current working hours
   */
  private patchFormWithMixedHours(
    workingHoursArrays: Array<Array<WorkingHour>>
  ): void {
    this.workingHoursForm.patchValue({
      scheduleType: this.scheduleTypeEnum.MixedHours
    });

    workingHoursArrays.forEach((hourGroup) => {
      hourGroup.forEach((hour) => {
        this.mixedHoursWeekdaysFormArray.controls[hour.weekDay - 1].patchValue({
          isSelected: true,
          opening: hour.opensAt,
          closing: hour.closesAt
        });
      });
    });

    this.workingHoursForm
      .get('fixedHours')
      .get('opening')
      .clearValidators();
    this.workingHoursForm
      .get('fixedHours')
      .get('closing')
      .clearValidators();
    this.workingHoursForm
      .get('fixedHours')
      .get('opening')
      .updateValueAndValidity();
    this.workingHoursForm
      .get('fixedHours')
      .get('closing')
      .updateValueAndValidity();
  }

  /**
   * Craeates a new Form Group for each day of the week and pushes it
   * to the mixed hours Form Array
   *
   */
  private setUpMixedHoursWeekdays(): void {
    this.weekdays.forEach(() => {
      const group: { [key: string]: FormControl } = {};

      (group.isSelected = new FormControl(false)),
        (group.opening = new FormControl('')),
        (group.closing = new FormControl(''));

      this.mixedHoursWeekdaysFormArray.push(new FormGroup(group));
    });
  }
}
